<template>
  <div class="top-bar">
    <router-link class="clickIcon" to="/" tag="div"></router-link>
    <nav class="top-nav">
      <!-- <span @click="event().selectedMenu(item.router,index)" v-for="(item,index) in navList" :key="index" :class="[activeIndex===index?'active':'']">{{item.menu}}</span> -->
      <router-link :class="[activeIndex===0?'active':'','link']" to="/data-analysis">{{$t('lang.dataStore')}}</router-link>
      <router-link :class="[activeIndex===1?'active':'','link']" to="/about">{{$t('lang.aboutUs')}}</router-link>
      <router-link :class="[activeIndex===2?'active':'','link']" to="/source">{{$t('lang.dataSource')}}</router-link>
      <img v-if="lang==='zh-CN'" @click="event().changeLangEvent()" style="margin-left:41px;cursor: pointer;" width="64px" height="32px" src="../assets/images/chinese.png" alt="">
      <img v-if="lang==='en-US'" @click="event().changeLangEvent()" style="margin-left:41px;cursor: pointer;" width="64px" height="32px" src="../assets/images/english.png" alt="">
    </nav>
  </div>
</template>

<script>
export default {
  data: () => {
    return {
      activeIndex: 0,
      lang:'zh-CN',
      language: true, //true:中文，false:英文
      navList: [
        { menu: "数据库", router: "/" },
        { menu: "关于我们", router: "/about" },
        { menu: "数据来源", router: "/source" }
      ]
    };
  },
  // created(){
  //   if (localStorage.getItem('lang')) {
  //     this.lang = localStorage.getItem('lang')
  //     this.$i18n.locale = localStorage.getItem('lang')
  //   }
  // },
  activated() {
    if (localStorage.getItem('lang')) {
      this.lang = localStorage.getItem('lang')
      this.$i18n.locale = localStorage.getItem('lang')
    }
  },
  methods: {
    event() {
      return {
        changeLangEvent:()=> {
          if (this.lang === "zh-CN") {
            localStorage.setItem('lang','en-US')
            this.lang = "en-US";
            this.$i18n.locale = this.lang; //关键语句
          } else {
            localStorage.setItem('lang','zh-CN')
            this.lang = "zh-CN";
            this.$i18n.locale = this.lang; //关键语句
          }
        },
        selectedMenu: (router, index) => {
          this.activeIndex = index;
          this.$router.push({ path: router });
        }
        //事件
      };
    }
  }
};
</script>

<style scoped lang="scss">
.top-bar {
  z-index: 10000;
  position: absolute;
  top: 0;
  width: 100%;
  height: 70px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  background-color: rgb(50, 173, 254);
  background-image: url("../assets/images/topbar.png");
  // background-position: 0 0;
  background-size: 1440px 70px;
  background-repeat: no-repeat;
  overflow: hidden;
  .clickIcon{
      position: absolute;
      left: 20px;
      top: 5px;
      width: 90px;
      height: 60px;
      cursor: pointer;
  }
  .top-nav {
    height: 34px;
    position: absolute;
    right: 0;
    top: 50%;
    margin-top: -17px;
    margin-right: 44px;
    display: flex;
    align-items: center;
    color: #d6eefe;
    .link {
      display: inline-block;
      font-size: 18px;
      cursor: pointer;
      margin: 0 20px;
      padding: 10px 15px;
      border: 2px solid transparent;
    }
    .active {
      border-bottom: 2px solid #fff;
      color: #fff;
    }
  }
}
// @media screen and (min-width: 1550px) {
